<template>
    <div class="news-center">
        <mt-header fixed title="动态"></mt-header>
        <div class="news-center-content">
            <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
                <ul>
                    <li v-for="(item, index) in list" :key="index">{{ item }}</li>
                </ul>
            </mt-loadmore>
        </div>
    </div>
</template>

<script>
import { Loadmore } from 'mint-ui';
export default {
    name: 'News',
    components: { Loadmore },
    data () {
        return {
            allLoaded: false,
            list: [1, 2, 3, 4, 5, 6],
            i: 7
        };
    },
    methods: {
        loadTop () {
            setTimeout(() => {
                // 数据加载完重新定位
                this.$refs.loadmore.onTopLoaded();
            }, 3000);
        },
        loadBottom () {
            setTimeout(() => {
                this.list.push('new data' + this.i++);
                // this.allLoaded = true;
                setTimeout(() => {
                    // 数据加载完重新定位
                    this.$refs.loadmore.onBottomLoaded();
                }, 0);
            }, 3000);
        }
    }
};
</script>

<style lang="less">
    .news-center{
        padding-top: 40px;
        &-content{
            .mint-cell{
                border-bottom: 1px solid #eee;
            }
            li{
                height: 100px;
                border-bottom: 1px solid #eee;
            }
        }
    }
</style>



