<template>
    <div class="user-center">
        <mt-header fixed title="我的">
            <mt-button
                icon="more"
                slot="right"
                @click="showMore">
            </mt-button>
        </mt-header>
        <div class="user-center-content">
            <mt-cell
                title="收藏"
                is-link
                to="/collection">
            </mt-cell>
        </div>
        <mt-actionsheet
            :actions="actions"
            v-model="sheetVisible">
        </mt-actionsheet>
    </div>
</template>

<script>
import { Actionsheet } from 'mint-ui';
export default {
    name: 'User',
    components: { Actionsheet },
    data () {
        return {
            sheetVisible: false,
            actions: [
                {
                    name: '切换用户',
                    method: () => {
                        this.changeUser();
                    }
                },
                {
                    name: '退出登录',
                    method: () => {
                        this.loginOut();
                    }
                }
            ]
        };
    },
    methods: {
        showMore () {
            this.sheetVisible = true;
        },
        loginOut () {
            this.$router.push('/login');
        },
        changeUser () {

        }
    }
};
</script>

<style lang="less">
    .user-center{
        padding-top: 40px;
        &-content{
            .mint-cell{
                border-bottom: 1px solid #eee;
            }
        }
    }
</style>



