                    <mt-swipe-item>2</mt-swipe-item>
                    <mt-swipe-item>3</mt-swipe-item>
                </mt-swipe>
            </div>
    </div>
</template>

<script>
import { Swipe, SwipeItem } from 'mint-ui';
export default {
    name: 'HomePage',
    components: { Swipe, SwipeItem },
    data () {
        return {
        };
    },
    created () {
    },
    methods: {
    }
};
</script>

<style lang="less">
    .home{
        &-swiper{
            height: 200px;
            background: #f02;
        }
    }
</style>



